<template>
  <div>
    <h1>Home Page</h1>
    <p>Welcome to the home page!</p>

    <hr>
    <div>
      <h1>User List</h1>
      <ul v-if="users.length">
        <li v-for="user in users" :key="user.id">{{ user.name }} {{ user.surname }}</li>
      </ul>
      <p v-else>No users found.</p>
    </div>

  </div>
</template>

<script>
import usersData from '../assets/data.json';

export default {
  name: 'HomePage',
  data() {
    return {
      users: usersData.users
    };
  }
}
</script>
