<template>
  <div>
    <h1>Home Page</h1>
    <p>Welcome to the home page!</p>
<button type="button" class="btn btn-primary">Primary</button>

    <hr>
    <div>
        <ul v-if="videoList.length">
            <li v-for="(video, index) in videoList" :key="index">
                <strong>UID:</strong> {{ video.uid }} <br/>
                <strong>UID2:</strong> {{ video.ms_video_preview_img_url }} <br/>
                <strong>UID3:</strong> {{ video.uid }} <br/>
                <strong>UID4:</strong> {{ video.uid }} <br/>
            </li>
        </ul>
        <p v-else>No video data found.</p>
    </div>

  </div>


    <div class="album py-5 bg-body-tertiary">
    <div class="container">

      <div class="row row-cols-1 row-cols-sm-2 row-cols-md-3 g-3" v-if="videoList.length">

        <div class="col" v-for="(video, index) in videoList" :key="index">
          <div class="card shadow-sm">
            <img :src="video.ms_video_preview_img_url" class="" width="100%" height="225">
            <div class="card-body">
              <p class="card-text">{{ video.ms_title }}</p>
              <p class="card-text truncate-text">{{ video.ms_description }}</p>
              <button class="btn btn-primary" @click="showModal(video)">Play Video</button>
              <!--
              <div class="d-flex justify-content-between align-items-center">
                <div class="btn-group">
                  <button type="button" class="btn btn-sm btn-outline-secondary">View</button>
                  <button type="button" class="btn btn-sm btn-outline-secondary">Edit</button>
                </div>
                <small class="text-body-secondary">9 mins</small>
              </div>
              -->
            
            </div>
          </div>
        </div>

      </div>

      <p v-else>No video data found.</p>

    </div>
  </div>



  <hr>
  <!-- Button trigger modal -->
<button type="button" class="btn btn-primary" data-bs-toggle="modal" data-bs-target="#itemModal">
  Launch demo modal
</button>

<!-- Modal -->
<div class="modal fade" id="itemModal" tabindex="-1" aria-labelledby="itemModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-xl modal-dialog-centered modal-dialog-scrollable">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="itemModalLabel">Modal title</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
      <div class="modal-body">

        <iframe width="100%" height="400" 
            src="https://www.youtube.com/embed/4RDP-LZoNmU" 
            frameborder="0" 
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
            allowfullscreen></iframe>


        {{ selectedItem.ms_title }}
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary">Save changes</button>
      </div>
    </div>
  </div>
</div>



  
</template>

<script>
import jsonData from '../../assets/data.json';

export default {
  name: 'HomePage',
  data() {
    return {
      videoList: [],
      selectedItem: {},

      modal: null
    };
  },
  created() {
    this.parseJsonData();
  },
  mounted() {
    this.modal = new window.bootstrap.Modal(document.getElementById('itemModal'))
  },
  methods: {
    parseJsonData() {
        //console.log(jsonData);
        //console.log(jsonData.ms_video_data);
        //console.log(Object.values(jsonData.ms_video_data));
        this.videoList = Object.values(jsonData.ms_video_data);
    },

    showModal(item) {
      this.selectedItem = item;
      // Use Bootstrap's JavaScript API to show the modal
      this.modal.show()
    },

    show() {
      this.modal.show()
    },
    hide() {
      this.modal.hide()
    }
  }
}
</script>

<style>
    .bd-placeholder-img {
    font-size: 1.125rem;
    text-anchor: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    }

    @media (min-width: 768px) {
    .bd-placeholder-img-lg {
        font-size: 3.5rem;
    }
    }

    .b-example-divider {
    width: 100%;
    height: 3rem;
    background-color: rgba(0, 0, 0, .1);
    border: solid rgba(0, 0, 0, .15);
    border-width: 1px 0;
    box-shadow: inset 0 .5em 1.5em rgba(0, 0, 0, .1), inset 0 .125em .5em rgba(0, 0, 0, .15);
    }

    .b-example-vr {
    flex-shrink: 0;
    width: 1.5rem;
    height: 100vh;
    }

    .bi {
    vertical-align: -.125em;
    fill: currentColor;
    }

    .nav-scroller {
    position: relative;
    z-index: 2;
    height: 2.75rem;
    overflow-y: hidden;
    }

    .nav-scroller .nav {
    display: flex;
    flex-wrap: nowrap;
    padding-bottom: 1rem;
    margin-top: -1px;
    overflow-x: auto;
    text-align: center;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    }

    .btn-bd-primary {
    --bd-violet-bg: #712cf9;
    --bd-violet-rgb: 112.520718, 44.062154, 249.437846;

    --bs-btn-font-weight: 600;
    --bs-btn-color: var(--bs-white);
    --bs-btn-bg: var(--bd-violet-bg);
    --bs-btn-border-color: var(--bd-violet-bg);
    --bs-btn-hover-color: var(--bs-white);
    --bs-btn-hover-bg: #6528e0;
    --bs-btn-hover-border-color: #6528e0;
    --bs-btn-focus-shadow-rgb: var(--bd-violet-rgb);
    --bs-btn-active-color: var(--bs-btn-hover-color);
    --bs-btn-active-bg: #5a23c8;
    --bs-btn-active-border-color: #5a23c8;
    }

    .bd-mode-toggle {
    z-index: 1500;
    }

    .bd-mode-toggle .dropdown-menu .active .bi {
    display: block !important;
    }

    .truncate-text {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    -webkit-line-clamp: 3;
    height: calc(1.2em * 3); /* Approximate height for 3 lines of text */
    line-height: 1.2em; /* Set this to match your line height */
    }

</style>